.formControl {
    margin: 5px 0;
}

.formControl label {
    display: block;
}   

.formControl input {
    width: 100%;
    border: 1px solid #ccc;
    border-radius: 5px;
    padding: 5px;
}

.formControl input:focus {
    outline: none;
}   

.formControl.error input,
.formControl.error textarea {
    border: 2px solid red;
}

.formControl span {
    color: red;
}

.formControl.error span {
    color: red;
}